#root {
  padding-top: 30px;
  color: #000957;
}

img {
  max-width: 100%;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  background: #AAB99A;
}

.goods-field {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  }

.goods-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border: 2px solid #000957;
  border-radius: 15px;
  padding: 20px;
}

.goods-block img {
  width: 100%;
  display: block;
  max-height: 230px;
  object-fit: contain;
  aspect-ratio: auto;
  height: 100%;
}

.img_wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.add-to-cart{
  padding: 8px 12px;
  border-radius: 10px;
  cursor: pointer;
  outline: none;
  border: none;
  font-size: 14px;
  font-weight: 600;
  background-color: #F0F0D7;
}

.main__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #727D73;
  padding: 10px 15px;
  border-radius: 15px;
}


.main__nav img {
  max-width: 42px;
  width: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav__list a {
  text-decoration: none;
  font-weight: 600;
  font-size: 18px;
  cursor: pointer;
}

.shop_cart-wrapper{
  position: relative;
}

.cart_count {
  position: absolute;
  top: -10px;
  right: 0;
  font-size: 20px;
  font-weight: 600;
  color: #000957;
  width: 24px;
  height: 20px;
  background-color: #EB5A3C;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 4px;
}

.empty_cart {
  font-size: 24px;
  font-weight: 600;
  color: #8E1616;
  text-align: center;
}

.active {
  color: #8E1616;
}

table{
 border: solid black 1px;
 text-align: center;
 border-collapse:collapse;
 width: 100%
}
td{
  border: solid black 1px;
  padding: 5px;
  width: 16%;
}
table img{
  width: 70px;
}

.minus, .plus, .delete {
  padding: 5px 10px;
  background-color: #F0F0D7;
  border: none;
  border-radius: 5px;
  margin-right: 5px;
}
.dont_show{
  display: none;
}

.show{
  display: block;
}

.footer {
  padding: 10px;
  background-color: #F0F0D7;
  border-radius: 10px;
}

.footer_text {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
}

.filter__nav {
  background: #727D73;
  padding: 5px;
  border-radius: 10px;
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.filter__btn {
  padding: 8px 12px;
    border-radius: 10px;
    cursor: pointer;
    outline: none;
    border: none;
    font-size: 14px;
    font-weight: 600;
    background-color: #F0F0D7;
}